<template>
  	<div class="content" id="MyLip">
  		<header class="head">
			<a class="back" href="javascript:;" @click="routeBack"></a>
			<h1 class="y-confirm-order-h1"></h1>
			<router-link to="/explain"><span class="mail"></span></router-link>
		</header>
		<div class="main">
			<div class="bonusWrap">				
				<div class="bonusMsg">
					<table>
						<tr class="title">
							<th></th>
							<th></th>
							<th></th>
							<th></th>
						</tr>
						<tr v-for="(item,index) in list">
							<td>{{item.add_time | time}}</td>
							<td v-if="item.status == 1"></td>
							<td v-else-if="item.status == 2"></td>
							<td v-else-if="item.status == 3"></td>
							<td v-else-if="item.status == 4"></td>
							<td v-else-if="item.status == 5"></td>
							<td v-else-if="item.status == 6"></td>
							<td v-else-if="item.status == 7"></td>
							<td v-else></td>
							<td>{{item.from_name}}</td>
							<td>{{item.amount}}</td>
						</tr>						
					</table>
				</div>
			</div>
		</div>
  	</div>
</template>
<script>
export default {

  data () {
    return {
      list : [],
      hasAjax:0,
	  page : 1
    }
  },
  created(){
    this.$store.commit('loading',{show:true,text:"..."});
    this.getData();
  },
  mounted(){
  	this.$store.commit('loading',{show:false});
  	this.loadMore();
  },
  methods: {
    getData(){
    	if( this.hasAjax == 0 ){
  			this.hasAjax = 1;
	    	this.$http.post('/Shop/User/myFenrun', {page:this.page},{emulateJSON:true}).then(function(response){
	    		this.hasAjax = 0;
	    		if( this.page == 0 ){
	            	this.list = response.data.data.list;	
	            	this.page++;  	    			
	    		}else{
	    			if( response.data.data.list == 0 ){
	    				this.hasAjax = 1;
	    			}else{
	    				this.page++;
	    				this.list = this.list.concat(response.data.data.list);
	    			}
	    		}	   
	    		this.$store.commit('loading',{show:false});     	
	        });  		
  		}    	
    },
    loadMore(){	        
        var that = this;
        this.$store.commit('scrollFun',{dom:'MyLip',auto:true,bottomCall:function(){
        	that.getData();
        }})
    },
    routeBack : function(){
      this.$router.go(-1);
    }
  }

}
</script>




