<template>
  	<div class="content">
  		<header class="head">
			<a class="back" href="javascript:;" @click="routeBack"></a>
			<h1 class="y-confirm-order-h1"></h1>
		</header>
		<div class="main">
			<div class="enr_input all">
				<form>
					<ul>
						<li class="line">
							<div class="einput">
								<span></span>
								<input type="text" name="" value="" placeholder="" v-model="dataJson.params">
							</div>
						</li>
						<li class="line">
							<div class="einput">
								<span></span>
								<input type="password" name="" placeholder="(6-12)" class="pmr" v-model="dataJson.password">
							</div>
						</li>
						<li class="line">
							<div class="einput">
								<input type="text" name="" placeholder="" class="code" v-model="dataJson.code">
								<a href="javascript:;" class="getCode_btn" id="getCode" @click="getCode" v-if="noget"></a>
								<a href="javascript:;" class="getCode_btn res" v-else>({{settimes}})</a>
							</div>
						</li>
						<li class="line">
							<div class="einput">
								<span></span>
								<input type="text" name="" value="" v-model="dataJson.invite">
							</div>
						</li>
						<li>
							<div class="einput">
								<span class="res"></span>
								<router-link to="/registrationProtocol" class="eag"></router-link>
							</div>
						</li>
					</ul>
				</form>
				<a href="javascript:;" class="register_btn" @click="registerFun"></a>
			</div>
		</div>
  	</div>
</template>
<script>
export default {

	data () {
	    return {
	      	dataJson : {},
	      	noget : 1,
          	settimes : 60
	    }
	},
	created(){
	    
	},
	methods: {
	    registerFun : function(){
	    	this.$http.post('/Shop/Login/register', this.dataJson,{emulateJSON:true}).then(function(response){	
				if( response.data.status == '200000' ){
					window.history.back();		
				}else{
					this.$store.commit('alert',{show:true,text:response.data.message});
				}    	
		    });
	    },
	    getCode : function () { //
            this.$http.post('/Shop/Login/getPhoneOrEmailVerify', {phone_or_email:this.dataJson.params,type:0},{emulateJSON:true}).then(function(response){	
				if( response.data.status == '200000' ){
					var GetCode = document.getElementById('getCode');
					this.settime(GetCode);				
				}else{
					this.$store.commit('alert',{show:true,text:response.data.message});
				}    	
		    });            
        },
	    settime : function (_this) {
            var that = this;
            if (that.settimes == 0) { 
                _this.innerText = "";
                that.noget = 1;
                that.settimes = 60; 
                return;
            } else { 
                _this.innerText = "("+ that.settimes +")";
                that.noget = 0;
                that.settimes--;
            } 
            setTimeout(function() { that.settime(_this) } ,1000) 
        },
	    routeBack : function(){
	      this.$router.go(-1);
	    }
	}

}
</script>




